import React from "react";
import css from './Info.css';
import sr from './scrollReveal.js';

 export class Info extends React.Component {

    componentDidMount () {
        const config = {
            origin: 'bottom',
      duration: 1000,
      delay: 150,
      distance: '500px',
      scale: 1,
      easing: 'ease',
        }
        sr.reveal(this.refs.brendi, config)

    }
    
    render(){
        return (
            <div ref="brendi" className='info' >
            <div className={css.inf}>
                <h3 className={css.tittle } >Implementa en tu empresa lo ltimo en sitios web y aplicaciones mviles </h3>
                <hr/>
                <p className={css.text + ' bot '}>
                    En Fixter puedes encontrar la solucin a tus problemas tecnolgicos.
                    Contamos con expertos en las tecnologas ms modernas del desarrollo, generando software de alta calidad, escalable, intuitiva y con buenas prcticas.
                </p>
                <div className={css.image}></div>
            </div>
            </div>

        );
    }
}

export default Info;




